import * as React from "react"
import Header from '../components/Header'
import { Link } from "gatsby"
import dotsw from '../../static/dotsw.png'
import bg from '../../static/dotbg.png'
import $ from 'jquery';
import { faArrowAltCircleRight, faArrowDown, faArrowRight, faBriefcase, faUser } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"

console.log('Home page loaded')

function view(){
  document.getElementById("hi").scrollIntoView({behavior:'smooth'});
}

export default function Home() {
  window.addEventListener("scroll",function(){
    var pageBottom = window.scrollY + window.screen.height;
    var tags = $(".appear");
    var elements = document.getElementsByClassName("appear");

    for (var i = 0; i < tags.length; i++) {

      var tag = tags[i];

    if (elements[i].offsetTop < pageBottom-200) 
      {
        $(tag).addClass("visible");
      } 
      else
      {
        $(tag).removeClass("visible");

      }
    }

  });
  return <div>
    <Header>

    <title>Home</title>
    <div className="flex flex-wrap items-center align-middle justify-evenly h-dvh bg-zinc-900">
      <img src={dotsw} id="bg" alt="dots" className=" absolute object-cover h-full rotate max-sm:h-2/3 opacity-60"/>
      <h1 className="text-5xl max-sm:text-2xl py-4 backdrop-blur-lg text-neutral-300 bg-zinc-900 w-full text-center">
        Welcome to&nbsp;
        <Link className=" text-red-600 rounded-full hover:px-5 hover:pb-1 hover:text-white hover:bg-red-600 duration-500">Anindya's</Link> Home!</h1>
        <div className="bg-red-600 text-white font-bold px-10 py-4 rounded-full duration-100 absolute bottom-14 lg:right-14 hover:bg-red-500 hover:animate-none animate-pulse hover:px-12" onClick={view}><FontAwesomeIcon icon={faArrowDown}/></div>

    </div>


    <div className="flex flex-wrap items-center align-middle justify-evenly bg-gray-50 dark:bg-neutral-900 min-h-screen max-h-fit">
      
      <div className="intro p-10 pb-1 rounded-3xl appear dark:text-neutral-200" id="slide2">
        <h1 className=" text-5xl mb-5" id="hi">Hi there! ðŸ‘‹ <br/> I'm Anindya Biswas...</h1>
        <p className="mb-10 ms-2 opacity-70">and this is my online home.</p>
        <p className="mb-1 ms-2 opacity-70"> I'm living my life, doing my engineering bachelors and making fun projects...</p>
        <p className="mb-14 ms-2 opacity-70">Check out my projects/works and know about me.</p>
        <div className="flex flex-wrap items-center align-middle">

        <Link to="/about" className=" text-2xl rounded-full py-4 px-7 m-2 text-red-100 bg-red-600 hover:bg-red-500 hover:ease-in-out duration-300">Works <FontAwesomeIcon icon={faArrowRight} size="xs" className=" ms-2"/></Link>
        <Link to="/about" className=" text-2xl rounded-full py-4 px-7 m-2 text-red-950 hover:text-red-100 border border-red-600 hover:bg-red-500 hover:ease-in-out duration-300 dark:text-red-100">About <FontAwesomeIcon icon={faArrowRight} size="xs" className=" ms-2"/></Link>
        </div>
      </div>
    </div>

    </Header>
    </div>
}

